import React, { Component } from 'react'
import { Link, Switch } from 'react-router-dom';
import Logo from '../cbn.png'
import authService from '../services/auth.service';


class Menu extends Component {

    constructor(props) {
        super(props);

        this.state = {
            Report_Officer: false,
            Branch_Officer: false,
            HQ_Officer: false,
            current_User: undefined,
            myloading: true
        };
    }

    componentDidMount() {

        //user  stored user information (including JWT) from AuthService class
        const user = authService.getCurrentUser();
        //check User Group
        if (user) {
            this.setState({
                current_User: user.profile,
                HQ_Officer: user.profile.user_groups.includes("HQ Officer"),
                Branch_Officer: user.profile.user_groups.includes("Branch Officer"),
                Report_Officer: user.profile.user_groups.includes("Report Officer"),
                
            });
        }
    }

    render() {
        const { current_User, HQ_Officer, Branch_Officer,Report_Officer } = this.state;


        return (

            <Switch>

                {current_User && (

                    <aside className="main-sidebar sidebar-light-success elevation-4">

                        {/* Brand Logo */}
                        <Link to="/dashboard" className="brand-link">
                            <center> <img src={Logo} height="150px" /></center><br />
                            <center> <span className="brand-text font-weight-light text-yellow"><h3><b>GFSO</b></h3></span></center>
                        </Link>

                        <div className="sidebar">

                            {/* Sidebar Menu */}
                            <nav className="mt-2">
                            {HQ_Officer && (

                                <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                                    {/* Add icons to the links using the .nav-icon class
                        with font-awesome or any other icon font library */}
                                    <li className="nav-item has-treeview menu-open">

                                        <Link to="/dashboard" className="nav-link active">
                                            <i className="nav-icon fas fa-home" />
                                            <p>
                                                Dashboard
                                        </p>
                                        </Link>

                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/Surveys" className="nav-link">
                                                    <i class="nav-icon fas fa-list text-green"></i>
                                                    <p>
                                                        All Surveys

</p>
                                                </Link>
                                            </li>
                                        </ul>


                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/ViewSurvey" className="nav-link">
                                                    <i class="nav-icon fas fa-table text-green"></i>
                                                    <p>
                                                        Survey Templates

</p>
                                                </Link>
                                            </li>
                                        </ul>

                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/ViewSurvey" className="nav-link">
                                                    <i class="nav-icon fas fa-bar-chart text-green"></i>
                                                    <p>
                                                        Survey Analysis

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/ViewSurvey" className="nav-link">
                                                    <i class="nav-icon fas fa-users text-green"></i>
                                                    <p>
                                                        User Management

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                    </li>
                                </ul>
                            )}
                                {Branch_Officer && (
                                <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                                    <li className="nav-item has-treeview menu-open">

                                        <Link to="/dashboard" className="nav-link active bg-primary">
                                            <i className="nav-icon fas fa-home" />
                                            <p>
                                                Dashboard
    </p>
                                        </Link>
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/AvailableSurvey" className="nav-link">
                                                    <i class="nav-icon fas fa-bullhorn text-primary"></i>
                                                    <p>
                                                        Take Survey

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/ViewSurvey" className="nav-link">
                                                    <i class="nav-icon fas fa-user text-primary"></i>
                                                    <p>
                                                        My Profile

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                       
                                    </li>

                                </ul>
                                )}
                                {Report_Officer && (

                                <ul className="nav nav-pills nav-sidebar flex-column" data-widget="treeview" role="menu" data-accordion="false">
                                    <li className="nav-item has-treeview menu-open">

                                        <Link to="/dashboard" className="nav-link active bg-warning">
                                            <i className="nav-icon fas fa-home" />
                                            <p>
                                                Dashboard
    </p>
                                        </Link>
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/listResponses" className="nav-link">
                                                    <i class="nav-icon fas fa-check-circle text-warning"></i>
                                                    <p>
                                                        Survey Responses

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        <ul class="nav nav-treeview">
                                            <li class="nav-item has-treeview">
                                                <Link to="/ViewSurvey" className="nav-link">
                                                    <i class="nav-icon fas fa-user text-warning"></i>
                                                    <p>
                                                        My Profile

</p>
                                                </Link>
                                            </li>
                                        </ul>
                                        
                                    </li>

                                </ul>
                                )}
                            </nav>
                            {/* /.sidebar-menu */}
                        </div>
                        {/* /.sidebar */}



                    </aside>

                )}
            </Switch>

        )
    }
}

export default Menu;